import Vue from "vue";
import App from "./App.vue";
import VueFormulate from "@braid/vue-formulate";
import "../node_modules/@braid/vue-formulate/dist/snow.min.css";
import S3UploaderPlugin from "./s3-uploader-plugin";

Vue.config.productionTip = false;

Vue.use(VueFormulate, {
  uploadUrl: "https://oqiqvt4aya.execute-api.us-east-2.amazonaws.com/PartsImageUploadSigner",
  plugins: [S3UploaderPlugin]
});

new Vue({
  render: (h) => h(App)
}).$mount("#app");
