<template>
  <FormulateForm @submit="handleSubmit">
    <FormulateInput type="text" label="Your email" name="email" />
    <FormulateInput
      type="file"
      name="documents"
      label="Your documents"
      placeholder="What issue are you having?"
      help="Select one or more PDFs to upload!"
      multiple
      validation="required|mime:application/pdf"
    />
    <FormulateInput type="submit" />
    <h4>Submitted Data</h4>
    <pre>{{ data }}</pre>
  </FormulateForm>
</template>

<script>
export default {
  data() {
    return {
      data: {},
    };
  },
  methods: {
    async handleSubmit(data) {
      this.data = data;
    },
  },
};
</script>
